.work-experience-card {
    color: #fff;
    border-radius: 0.65rem;
    border: 1.5px solid var(--coloBtnHire);
    background: rgba(27, 17, 47, 0.398);
    backdrop-filter: blur(1rem);
    padding: 1.5rem;
    margin: 0 0.1rem;
    min-height: 250px !important;
    transition: all 0.3s ease;
    cursor: pointer;
}

.work-experience-card:hover {
    background: rgba(24, 56, 83, 0.398);
}

.work-experience-card h6 {
    margin: 0;
    font-size: 1.07rem;
    font-weight: 500;
    margin-bottom: 0.7rem;
}

.work-duration {
    display: inline-block;
    font-size: 0.7rem;
    font-weight: 400;
    background: linear-gradient(
        90deg,
        var(--colorNavMobile) 0%,
        var(--coloSecBtnHire) 100%
    );
    padding: 0.4rem 0.6rem;
    margin-bottom: 0.1rem;
    border-radius: 0.1rem;
}

.work-experience-card ul li {
    list-style: none;
    font-size: 0.8rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
    position: relative;
}

.work-experience-card ul li::after {
    content: " ";
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 0.5rem;
    background: linear-gradient(
        90deg,
        var(--coloBtnHire) 0%,
        var(--coloSecBtnHire) 100%
    );
    position: absolute;
    left: -1.3rem;
    top: 0.35rem;
}
