.contact-form-content {
    width: 100%;
    color: #fff;
}

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.5rem;
}

.name-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 1rem;
}

form input,
form textarea {
    flex: 1;
    font-size: 0.9rem;
    color: #fff;
    background-color: rgba(22, 17, 47, 0.398);
    border-radius: 0.6rem;
    border: 1.5px solid var(--coloBtnHire);
    padding: 1rem;
}

form button {
    font-size: 1rem;
    font-weight: 500;
    color: #fff;
    border-radius: 0.6rem;
    border: 1.5px solid var(--coloBtnHire);
    padding: 0.7rem;
    transition: all 0.3s ease;
    cursor: pointer;
    background: linear-gradient(
        90deg,
        var(--coloBtnHire) 0%,
        var(--coloSecBtnHire) 100%
    );
}

form button:hover {
    color: var(--coloBtnHire);
    background: none;
}

@media (max-width: 450px) {
    .name-container {
        flex-direction: column;
        align-items: flex-start;
    }

    form input,
    form textarea {
        padding: 0.7rem;
        border-radius: 0.3rem;
    }

    form button {
        font-size: 0.9rem;
        padding: 0.7rem;
        border-radius: 0.3rem;
    }
}
